尔市\",\"text\":\"新疆维吾尔自治区-自治区直辖县级行政区划-阿拉尔市\"},{\"id\":\"659003\",\"pId\":\"659000\",\"name\":\"[659003] 图木舒克市\",\"text\":\"新疆维吾尔自治区-自治区直辖县级行政区划-图木舒克市\"},{\"id\":\"659004\",\"pId\":\"659000\",\"name\":\"[659004] 五家渠市\",\"text\":\"新疆维吾尔自治区-自治区直辖县级行政区划-五家渠市\"},{\"id\":\"710000\",\"pId\":null,\"name\":\"[710000] 台湾省\",\"text\":\"台湾省\"},{\"id\":\"810000\",\"pId\":null,\"name\":\"[810000] 香港特别行政区\",\"text\":\"香港特别行政区\"},{\"id\":\"820000\",\"pId\":null,\"name\":\"[820000] 澳门特别行政区\",\"text\":\"澳门特别行政区\"}]"
    }
  },
  props: ['lastCode'], // 如果是编辑时父组件传递过来的地址编码（新增是undefined）
  // mounted () {  //   this.getAddress() 貌似行不通  // },
  methods: {
    getAddress (initCode) {
      // 获得哥给的未排版地址 list，全部都是同层的
      // API.store.getAddress().then(result => {
      // if (result.code === '0000') {
      if (true) {
        console.log("陈希")
        var arr = []
        // var addr = JSON.parse(result.data)
        var addr = JSON.parse(this.List)
        console.log(addr)

        addr.forEach(item => {
          console.log(this.lastCode)
          if (initCode !== undefined) {
            if (initCode === item.id) {
              this.init.unshift(item.id)
              this.init.unshift(item.pId)
            }
          }
          if (item.pId === null) { arr.push({value: item.id, label: item.name.slice(9), children: []}) }
        })
        if (initCode !== undefined) {
          addr.forEach(item => {
            if (this.init[0] === item.id) {
              this.init.unshift(item.pId)
            }
          })
        }
        console.log(this.init)
        addr.forEach(item => {
          if (item.pId !== null) {
            arr.forEach(i => {
              if (i.value === item.pId) {
                i.children.push({value: item.id, label: item.name.slice(9), children: []})
                return false
              }
            })
          }
        })
        console.log(arr)
        addr.forEach(item => {
          if (item.pId !== null) {
            arr.forEach(i => {
              i.children.forEach(j => {
                if (j.value === item.pId) {
                  j.children.push({value: item.id, label: item.name.slice(9)})
                  return false
                }
              })
            })
          }
        })
        console.log(arr)

        this.allAddressList = arr // 三级级联地址的所有官方数据
        // console.log(this.arr)
        this.childCode = this.init[2]
      }
      // })
    },
    handleChange (val) {
      // console.log(val)// console.log(this.arr) el-cascader中的v-model='arr' 和此处val同一个值，都是数组
      this.childCode = val[2] // 获得（三级）最后一级的编码
      this.$emit('chenxi', this.childCode)
    }

  }
}
</script>

